<template>
    <div class="container">
        <!--header-->
        <div class="header">
            <span class="header-goBack" @click="goBack"><</span>
            通讯录
        </div>
        <!--content-->
        <div class="content">
            <div v-for="item in list" :key="item" class="content-each">
                {{item.name}}
            </div>
        </div>
        <Footer></Footer>
    </div>
</template>
<script>
import Footer from './Footer'
export default {
    name: 'Address',
    methods:{
        goBack(){
             this.$router.back(-1);
        }
    },
    components:{
        Footer
    },
    data(){
        return {
            list:[{
                name: '尹铮',
                phone: 18000351426
            },{
                name: '尹铮',
                phone: 18000351426
            },{
                name: '尹铮',
                phone: 18000351426
            },{
                name: '尹铮',
                phone: 18000351426
            },{
                name: '尹铮',
                phone: 18000351426
            },{
                name: '尹铮',
                phone: 18000351426
            }]
        }
    }
}
</script>
<style scoped>
.container{
    height: 100%;
    width: 100%;
    background: #fff;
    box-sizing: border-box;
    padding-bottom: 6.3rem;
}
.header{
    height: 2.5rem;
    text-align: center;
    color: #fff;
    background: rgb(30,129,210);
    line-height: 2.5rem;
}
.header-goBack{
    position: absolute;
    left: 0.3rem;
}
.content{
    padding: 0.5rem;
    background: gray;
    height: 100%;
   
    box-sizing: border-box;
}
.content-each{
    border-bottom: 1px solid #808080;
    height: 2rem;
    line-height: 2rem;
    padding: 0 0.5rem;
    background: #fff;
}
</style>



